.up-daterangepicker {
  position: absolute;
  color: inherit;
  background-color: #fff;
  border-radius: 4px;
  width: 270px;
  padding: 4px;
  margin-top: 1px;
  top: 100px;
  left: 20px;
  /* Calendars */
}

.up-daterangepicker:before, .up-daterangepicker:after {
  position: absolute;
  display: inline-block;
  border-bottom-color: rgba(0, 0, 0, 0.2);
  content: '';
}

.up-daterangepicker:before {
  top: -7px;
  border-right: 7px solid transparent;
  border-left: 7px solid transparent;
  border-bottom: 7px solid #ccc;
}

.up-daterangepicker:after {
  top: -6px;
  border-right: 6px solid transparent;
  border-bottom: 6px solid #fff;
  border-left: 6px solid transparent;
}

.up-daterangepicker.opensleft:before {
  right: 9px;
}

.up-daterangepicker.opensleft:after {
  right: 10px;
}

.up-daterangepicker.up-openscenter:before {
  left: 0;
  right: 0;
  width: 0;
  margin-left: auto;
  margin-right: auto;
}

.up-daterangepicker.up-openscenter:after {
  left: 0;
  right: 0;
  width: 0;
  margin-left: auto;
  margin-right: auto;
}

.up-daterangepicker.up-opensright:before {
  left: 9px;
}

.up-daterangepicker.up-opensright:after {
  left: 10px;
}

.up-daterangepicker.up-dropup {
  margin-top: -5px;
}

.up-daterangepicker.up-dropup:before {
  top: initial;
  bottom: -7px;
  border-bottom: initial;
  border-top: 7px solid #ccc;
}

.up-daterangepicker.up-dropup:after {
  top: initial;
  bottom: -6px;
  border-bottom: initial;
  border-top: 6px solid #fff;
}

.up-daterangepicker.up-dropdown-menu {
  max-width: none;
  z-index: 111119001;
}

.up-daterangepicker.up-single .up-ranges, .up-daterangepicker.up-single .up-calendar {
  float: none;
}

.up-daterangepicker.up-show-calendar .up-calendar {
  display: block;
}

.up-daterangepicker .up-calendar {
  display: none;
  max-width: 262px;
  margin: 4px;
}

.up-daterangepicker .up-calendar.up-single .up-calendar-table {
  border: none;
}

.up-daterangepicker .up-calendar th, .up-daterangepicker .up-calendar td {
  white-space: nowrap;
  text-align: center;
  min-width: 28px;
}

.up-daterangepicker .up-calendar-table {
  border: 1px solid #fff;
  padding: 4px;
  border-radius: 4px;
  background-color: #fff;
}

.up-daterangepicker table {
  width: 100%;
  margin: 0;
}

.up-daterangepicker td, .up-daterangepicker th {
  text-align: center;
  width: 20px;
  height: 20px;
  border-radius: 4px;
  border: 1px solid transparent;
  white-space: nowrap;
  cursor: pointer;
}

.up-daterangepicker td.up-available:hover, .up-daterangepicker th.up-available:hover {
  background-color: #eee;
  border-color: transparent;
  color: inherit;
}

.up-daterangepicker td.up-week, .up-daterangepicker th.up-week {
  font-size: 80%;
  color: #ccc;
}

.up-daterangepicker td.up-off, .up-daterangepicker td.up-off.up-in-range, .up-daterangepicker td.up-off.up-start-date, .up-daterangepicker td.up-off.up-end-date {
  background-color: #fff;
  border-color: transparent;
  color: #999;
}

.up-daterangepicker td.up-in-range {
  background-color: #ebf4f8;
  border-color: transparent;
  color: #000;
  border-radius: 0;
}

.up-daterangepicker td.up-start-date {
  border-radius: 4px 0 0 4px;
}

.up-daterangepicker td.up-end-date {
  border-radius: 0 4px 4px 0;
}

.up-daterangepicker td.up-start-date.end-date {
  border-radius: 4px;
}

.up-daterangepicker td.up-active, .up-daterangepicker td.up-active:hover {
  background-color: #27a7a9;
  border-color: transparent;
  color: #fff;
}

.up-daterangepicker td.up-dismonth {
  cursor: default;
}

.up-daterangepicker th.up-month {
  width: auto;
}

.up-daterangepicker td.up-disabled, .up-daterangepicker option.up-disabled {
  color: #999;
  cursor: not-allowed;
  text-decoration: line-through;
}

.up-daterangepicker select.up-monthselect, .up-daterangepicker select.up-yearselect {
  font-size: 12px;
  padding: 1px;
  height: auto;
  margin: 0;
  cursor: default;
  display: inline-block;
  border-radius: 3px;
  border: 1px solid #ccc;
  height: 25px;
  background-color: #fff;
}

.up-daterangepicker select.up-monthselect {
  margin-right: 10%;
  width: 46%;
}

.up-daterangepicker select.up-yearselect {
  width: 46%;
}

.up-daterangepicker select.up-hourselect, .up-daterangepicker select.up-minuteselect, .up-daterangepicker select.up-secondselect, .up-daterangepicker select.up-ampmselect {
  width: 56px;
  margin: 0 1px;
  display: inline-block;
  border-radius: 3px;
  border: 1px solid #ccc;
  height: 25px;
  background-color: #fff;
}

.up-daterangepicker .up-input-mini {
  border: 1px solid #ccc;
  border-radius: 4px;
  color: #555;
  height: 30px;
  line-height: 30px;
  display: block;
  vertical-align: middle;
  margin: 0 0 5px 0;
  padding: 0 6px 0 28px;
  width: 100%;
}

.up-daterangepicker .up-input-mini.up-active {
  border: 1px solid #08c;
  border-radius: 4px;
}

.up-daterangepicker .up-daterangepicker_input {
  position: relative;
}

.up-daterangepicker .up-daterangepicker_input i {
  position: absolute;
  left: 8px;
  top: 1px;
  font-size: 18px;
}

.up-daterangepicker.up-rtl .input-mini {
  padding-right: 28px;
  padding-left: 6px;
}

.up-daterangepicker.up-rtl .up-daterangepicker_input i {
  left: auto;
  right: 8px;
}

.up-daterangepicker .up-calendar-time {
  text-align: left;
  margin: 5px auto;
  line-height: 30px;
  position: relative;
  padding-left: 65px;
}

.up-daterangepicker .up-calendar-time select.up-disabled {
  color: #ccc;
  cursor: not-allowed;
}

.up-ranges {
  font-size: 11px;
  float: none;
  margin: 4px;
  text-align: left;
}

.up-ranges ul {
  list-style: none;
  margin: 0 auto;
  padding: 0;
  width: 100%;
}

.up-ranges li {
  font-size: 13px;
  background-color: #f5f5f5;
  border: 1px solid #f5f5f5;
  border-radius: 4px;
  color: #08c;
  padding: 3px 12px;
  margin-bottom: 8px;
  cursor: pointer;
  text-align: center;
}

.up-ranges li:hover {
  background-color: #08c;
  border: 1px solid #08c;
  color: #fff;
}

.up-ranges li.up-active {
  background-color: #08c;
  border: 1px solid #08c;
  color: #fff;
}

/*  Larger Screen Styling */
@media (min-width: 564px) {
  .up-daterangepicker {
    width: auto;
  }
  .up-daterangepicker .up-ranges ul {
    width: 95px;
  }
  .up-daterangepicker.up-single .up-ranges ul {
    width: 100%;
  }
  .up-daterangepicker.up-single .up-calendar.up-left {
    clear: none;
  }
  .up-daterangepicker.up-single.up-ltr .up-ranges, .up-daterangepicker.up-single.up-ltr .up-calendar {
    float: left;
  }
  .up-daterangepicker.up-single.up-rtl .up-ranges, .up-daterangepicker.up-single.up-rtl .up-calendar {
    float: right;
  }
  .up-daterangepicker.up-ltr {
    direction: ltr;
    text-align: left;
  }
  .up-daterangepicker.up-ltr .up-calendar.up-left {
    clear: left;
    margin-right: 0;
  }
  .up-daterangepicker.up-ltr .up-calendar.up-left .up-calendar-table {
    border-right: none;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }
  .up-daterangepicker.up-ltr .up-calendar.up-right {
    margin-left: 0;
  }
  .up-daterangepicker.up-ltr .up-calendar.up-right .up-calendar-table {
    border-left: none;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }
  .up-daterangepicker.up-ltr .up-left .up-daterangepicker_input {
    padding-right: 12px;
  }
  .up-daterangepicker.up-ltr .up-calendar.up-left .up-calendar-table {
    padding-right: 12px;
  }
  .up-daterangepicker.up-ltr .up-ranges, .up-daterangepicker.up-ltr .up-calendar {
    float: left;
  }
  .up-daterangepicker.up-rtl {
    direction: rtl;
    text-align: right;
  }
  .up-daterangepicker.up-rtl .up-calendar.up-left {
    clear: right;
    margin-left: 0;
  }
  .up-daterangepicker.up-rtl .up-calendar.up-left .up-calendar-table {
    border-left: none;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }
  .up-daterangepicker.up-rtl .up-calendar.up-right {
    margin-right: 0;
  }
  .up-daterangepicker.up-rtl .up-calendar.up-right .up-calendar-table {
    border-right: none;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }
  .up-daterangepicker.up-rtl .up-left .up-daterangepicker_input {
    padding-left: 12px;
  }
  .up-daterangepicker.up-rtl .up-calendar.up-left .up-calendar-table {
    padding-left: 12px;
  }
  .up-daterangepicker.up-rtl .up-ranges, .up-daterangepicker.up-rtl .up-calendar {
    text-align: right;
    float: right;
  }
}

@media (min-width: 730px) {
  .up-daterangepicker .up-ranges {
    width: auto;
  }
  .up-daterangepicker.up-ltr .up-ranges {
    float: left;
  }
  .up-daterangepicker.up-rtl .up-ranges {
    float: right;
  }
  .up-daterangepicker .up-calendar.up-left {
    clear: none !important;
  }
}
